  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__product-image {
  width: 160px;
  margin: 0 auto;
  padding-top: 16px;
}

.card__product-image img {
  vertical-align: bottom;
